@font-face {
  font-family: "volt";
  src: url("/static/fonts/Voltage Bold.otf"); }

@font-face {
  font-family: "raleway-extrabold";
  src: url("/static/fonts/Raleway-ExtraBold.ttf"); }

@font-face {
  font-family: "raleway-light";
  src: url("/static/fonts/Raleway-Light.ttf"); }

@font-face {
  font-family: "raleway-regular";
  src: url("/static/fonts/Raleway-Regular.ttf"); }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

li {
  list-style: none; }

.icon {
  margin-left: 5px;
  transform: translateY(-2.5px); }

.main_button {
  cursor: pointer;
  border: none;
  min-width: 225px;
  text-align: center;
  color: white;
  background: #032c4a;
  font-family: "raleway-extrabold";
  font-size: 18px;
  letter-spacing: 1px;
  height: 50px;
  margin-top: 25px;
  margin-right: 25px; }

html {
  background: #76b3a8; }
