@font-face {
  font-family: "volt";
  src: url("/static/fonts/Voltage Bold.otf"); }

@font-face {
  font-family: "raleway-extrabold";
  src: url("/static/fonts/Raleway-ExtraBold.ttf"); }

@font-face {
  font-family: "raleway-light";
  src: url("/static/fonts/Raleway-Light.ttf"); }

@font-face {
  font-family: "raleway-regular";
  src: url("/static/fonts/Raleway-Regular.ttf"); }

.home .home_header {
  width: 100%;
  background: url("/static/images/ws_home_header.svg") no-repeat;
  background-size: cover;
  background-position: bottom;
  min-height: 750px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2%; }
  .home .home_header h1 {
    max-width: 1100px;
    color: white;
    font-family: "raleway-regular";
    font-size: 52px;
    text-align: center; }
    .home .home_header h1 span {
      color: #a6d5cd; }
  @media screen and (max-width: 1200px) {
    .home .home_header {
      min-height: 600px; }
      .home .home_header h1 {
        font-size: 42px; } }
  @media screen and (max-width: 960px) {
    .home .home_header h1 {
      font-size: 36px; } }

.home .sub_header {
  background: #76b3a8;
  padding: 150px 3%; }
  .home .sub_header h2 {
    color: #032c4a;
    font-family: "volt", sans-serif;
    font-size: 48px;
    margin-bottom: 25px; }
  .home .sub_header p {
    color: white;
    font-family: "raleway-regular";
    font-size: 22px;
    line-height: 1.5; }
  .home .sub_header img {
    min-height: 250px; }
  .home .sub_header .description {
    max-width: 550px; }
  .home .sub_header .what_we_do,
  .home .sub_header .you_can_help {
    max-width: 1300px;
    margin: auto;
    display: flex;
    justify-content: space-around; }
  .home .sub_header .you_can_help {
    padding-top: 150px; }
    .home .sub_header .you_can_help a {
      margin: 0;
      width: 200px;
      color: white;
      text-decoration: none; }
  @media screen and (max-width: 1200px) {
    .home .sub_header {
      padding: 75px 3%; }
      .home .sub_header .what_we_do,
      .home .sub_header .you_can_help {
        display: block; }
        .home .sub_header .what_we_do .description,
        .home .sub_header .you_can_help .description {
          margin: auto;
          text-align: center; }
        .home .sub_header .what_we_do img:first-child,
        .home .sub_header .you_can_help img:first-child {
          display: none; }
        .home .sub_header .what_we_do img:last-child,
        .home .sub_header .you_can_help img:last-child {
          padding-top: 75px; }
      .home .sub_header .you_can_help {
        padding-top: 75px; } }

.home .home_news_and_podcasts {
  padding: 150px 3%; }

.home .home_events_feed {
  background: #032c4a;
  padding: 150px 3%; }
  .home .home_events_feed h1 {
    color: #e9df81;
    text-align: center;
    font-size: 64px;
    font-family: "volt", sans-serif; }
  .home .home_events_feed .event_slider {
    display: flex; }

@media screen and (max-width: 1200px) {
  .home .home_news_and_podcasts,
  .home .home_events_feed {
    padding: 75px 3%; } }

.home_footer_image {
  transform: translateY(10px); }
